<template>
  <transition name="slide-toggle">
    <div :class="`${prefixCls}-date-picker clearfix`" v-if="showHide" :style="pickerStyle" @click="_stopPropagation">
      <div class="calendar">
        <div class="calendar-head">
          <a class="calendar-prev-year" @click="_yearClick(0)"></a>
          <a class="calendar-prev-month" @click="_monthClick(0)" v-if="showMonthControl&&activePanel!='Year'"></a>
          <a class="calendar-year-select" @click="activePanel='Year'">{{year}}</a>
          <a class="calendar-year-month" @click="activePanel='Month'" v-if="showMonth">{{month}}</a>
          <a class="calendar-next-month" @click="_monthClick(1)" v-if="showMonthControl&&activePanel!='Year'"></a>
          <a class="calendar-next-year" @click="_yearClick(1)"></a>
        </div>
        <div class="calendar-body">
          <component
            v-model="activeValue"
            :is="activePanel"
            :bodyValue="bodyValue"
            :type="type"
            @click="_click">
          </component>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import Year from './year'
import Month from './month'
import Day from './day'
import {prefixCls} from '../prefix'

export default {
  name: 'datePicker',
  data () {
    return {
      prefixCls: prefixCls,
      activeValue: '', // 
      bodyValue: '', // 
      showHide: true,
      activePanel: '' // 
    }
  },
  watch: {},
  props: {
    value: [Date, String, Number],
    disabledDate: {
      type: Function,
      default: function () {
        return false
      }
    },
    type: {
      type: String,
      default: 'date',
      validator: function (value) {
        // ///
        return ['year', 'month', 'date', 'datetime'].indexOf(value) !== -1
      }
    },
    offset: Object, // 
    input: Function, // 
    innerHTML: Function,
    downStyle: Object, // 
    positionTop: Boolean
  },
  components: {Year, Month, Day},
  created () {
    this.activeValue = this._getTime(this.value) // 
    this.bodyValue = this._getTime(this.value)
    this._setActivePanel() // 
  },
  methods: {
    _yearClick (type) {
      // 
      // type=01
      // +10+-1
      let num = 1
      if (this.activePanel === 'Year') {
        num = 10
      }
      if (type === 0) {
        num = -num
      }
      // 
      if (this.activePanel === 'Year') {
        this.bodyValue = new Date(this.bodyValue.setYear(this.bodyValue.getFullYear() + num))
      } else {
        this.activeValue = new Date(this.activeValue.setYear(this.activeValue.getFullYear() + num))
      }
    },
    _monthClick (type) {
      // 
      // type=01
      let num = 1
      if (type === 0) {
        num = -num
      }
      this.activeValue = new Date(this.activeValue.setMonth(this.activeValue.getMonth() + num))
    },
    _getTime (date) {
      // 
      let time
      if (date) {
        time = new Date(date.toString())
        if (time.toString() === 'Invalid Date') {
          // 
          time = new Date()
        }
      } else {
        // 
        time = new Date()
      }
      return time
    },
    close () {
      this.showHide = false
    },
    _stopPropagation (e) {
      e.stopPropagation()
    },
    _click (value) {
      // 
      console.log('click')
      console.log(value)
      if (this.activePanel === 'Year') {
        // valuestring  2019
        // 
        if (this.type === 'year') {
          // 
          this.input(value)
          this.close()
        } else {
          // 
          // activeValue
          this.activeValue = new Date(this.activeValue.setYear(value))
          this.activePanel = 'Month'
        }
      } else if (this.activePanel === 'Month') {
        // value string Wed May 01 2019
        // 
        // 
        if (this.type === 'month') {
          this.input(value)
          this.close()
        } else {
          // 
          const month = new Date(value).getMonth()
          this.activeValue = new Date(this.activeValue.setMonth(month))
          this.activePanel = 'Day'
        }
      } else {
        // 
        this.input(value)
        this.close()
      }
    },
    _setActivePanel () {
      // 
      if (this.type === 'year') {
        // 
        this.activePanel = 'Year'
      } else if (this.type === 'month') {
        this.activePanel = 'Month'
      } else {
        this.activePanel = 'Day'
      }
    }
  },
  computed: {
    year () {
      return new Date(this.activeValue).getFullYear()
    },
    month () {
      return new Date(this.activeValue).getMonth() + 1
    },
    pickerStyle () {
      // 
      let style = null
      if (this.offset) {
        style = {
          left: this.offset.left + 'px',
          top: this.offset.top + this.offset.height + 'px'
        }
        if (this.positionTop) {
          style = {
            left: this.offset.left + 'px',
            bottom: this.offset.top + this.offset.height + 'px'
          }
        }
      }
      if (this.downStyle) {
        style = Object.assign({}, style, this.downStyle)
      }
      return style
    },
    showMonthControl () {
      // 
      // return this.type.length > 2
      return this.type === 'date' || this.type === 'datetime'
    },
    showMonth () {
      // type='y'
      return this.type !== 'year'
    }
  },
  mounted () {
    // document.body.appendChild(this.$el)
  },
  destroyed () {
    // this.$el.parentNode.removeChild(this.$el)
  },
  filters: {}
}
</script>
