<template>
  <form :class="cls">
    <slot></slot>
  </form>
</template>

<script>
import {prefixCls} from '../prefix'

export default {
  name: `${prefixCls}Form`,
  componentName: `${prefixCls}Form`,
  data() {
    return {
      fields: [], // 
      defaultModel: {} // 
    }
  },
  props: {
    rules: {
      type: Object,
      default: () => {
        return {}
      }
    },
    showMessage: {
      type: Boolean,
      default: true
    },
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    trigger: {
      // changeblurinput
      default: 'change',
      validator: function (value) {
        return ['change', 'blur'].indexOf(value) !== -1
      }
    },
    labelWidth: String
  },
  components: {},
  created() {
  },
  methods: {
    validate(props) {
      let fields = this.fields
      if (props && props.length > 0) {
        fields = this.fields.filter((field) => {
          return props.indexOf(field.prop) !== -1
        })
      }
      return new Promise((resolve, reject) => {
        this._validateComm(fields)
          .then(res => {
            resolve(res) // 
          })
          .catch(res => {
            reject(res)
          })
      })
    },
    _validateComm(fields) {
      let valid = true
      let count = 0
      let errorTips = []
      console.log('validate fields')
      console.log(fields)
      return new Promise((resolve, reject) => {
        if (fields.length > 0) {
          fields.forEach(field => {
            // formItemvalidate
            // console.log(field)
            field.validate((result, field) => {
              if (result !== true) {
                // 
                valid = false
                errorTips.push(result)
              }
              if (++count === fields.length) {
                if (valid) {
                  errorTips = this.value
                } // value
                if (valid) {
                  // 
                  resolve(errorTips)
                } else {
                  reject(errorTips)
                }
              }
            })
          })
        } else {
          resolve()
        }
      })
    },
    resetFields() {
      // 
      this.$emit('input', Object.assign({}, this.defaultModel))
      // 
      this.fields.forEach(field => {
        field.resetField()
      })
    }
  },
  computed: {
    cls() {
      return `${prefixCls}-form`
    }
  },
  mounted() {
    // 
    this.defaultModel = Object.assign({}, this.value)
  }
}
</script>
